.services {
  min-height: 400px;
  width: 100%;
  &__container {
    max-width: 1820px;
    margin: 0 auto;
  }
  &__wrapper {
    max-width: 890px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    margin-bottom: 80px;
  }

  &__title-container { 
    width: 43%;
    margin: auto 0;
  }

  &__title {
    font-size: 30px;
    text-align: center;
    margin-right: 40px;
    line-height: 1.25 ;
  }

  &__subtitle {
    width: 75%;
    font-size: 18px;
    line-height: 1.3;
  }

  &__item-wrapper {
    display: flex;
    flex-direction: row;
    margin-bottom: 50px;
 
  }
  &__item {
    width: 25%;
    margin: 0 10px;
    display: flex;
    flex-wrap: wrap;
    &-even {
    flex-direction: column-reverse;
    }
  }

  &__title-wrapper {
    max-width: 390px;
    display: flex;
    justify-content: flex-start;
    flex-wrap: nowrap;
    margin-bottom: 20px;

  }

  &__title-img {
   margin-right: 6px;
  }

  &__text-container {
    padding-top: 10px;

  }
  &__item-title {
    font-size: 24px;
    font-family: $Quest;
  }

  &__item-description {
    margin-left: 30px;
    font-size: 16px;
    font-family: $Quest;
    line-height: 1.4;
    margin-bottom: 30px;
  }

 &__picture-wrapper {
   display: flex;
   justify-content: center;
 } 

}