.header {
  &__contact {
    background-color: $blue;
    min-height: 40px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-end;
    align-items: center;
    color: $white;
    padding-right: 200px;
  }

  &__contact-item {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    margin: 0 15px;
  
  }
  &__contact-text {
    margin: auto 0; 
    color: $white;
    margin-left: 10px;
    &:hover {
      color: $gray !important;
    }
    &:active {
      color: $yellow !important;
    }
  }
  
  &__cotainer {
    max-width: 1500px;
    margin: 0 auto;
    min-height: 108px;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  &__nav {
    padding-top: 16px;
    display: flex;
    justify-content: center;
  }
  &__nav-item  {
    margin: 0 15px;
    text-transform: uppercase;
    color: $darkGray;
    &--active {
      color: $yellow !important;
    }
  }

  &__search {
    padding-top: 16px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
  }

  &__search-item {
    max-width: 150px;
    outline: none;
    border:none;
    padding: 3px;
    font-size: 18px;
    margin-right: 5px;
    margin-bottom: 3px;
    font-style: italic;
    font-weight: 300;
    color:$gray;
  }

  &__search-btn {
    width: 200px;
    height: 42px;
    background-color: $blue;
    outline: none;
    font-weight: 600;
    font-style: italic;
    font-size: 20px;
    border:transparent;
    border-radius: 20px;  
    color: $white;
    &:hover {
      color:$darkGray;
      background-color: darken($blue, $amount: 5%);
    }
    &:active {
      color: $yellow;
      background-color: darken($blue, $amount: 5%);
    }
  }
    
}