.footer {
  width: 100%;
  background-color: $gray;
  min-height: 280px;
  border-bottom: 1px solid $darkGray;
  &__container {
    padding-top: 30px;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
  &__left {
    text-transform: uppercase;
      color: $white;
  }
  &__title {
    font-family: $Quest;
    font-size: 30px;
    margin-bottom: 20px;

  }
  &__adress {
    font-family: $Quest;
    font-size: 16px;
    margin-bottom: 20px;
  }

  &__right {
    text-align: center;
  }
  &__nav {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
  }
  &__nav-item  {
    font-size: 14px;
    margin: 0 15px;
    text-transform: uppercase;
    color: $white;
  } 

  &__button {
    width: 200px;
    height: 42px;
    background-color: $white;
    outline: none;
    font-weight: 600;
    font-style: italic;
    font-size: 20px;
    border:transparent;
    border-radius: 20px;  
    color: $blue;
    margin-bottom: 60px;
    &:hover {
      color:$darkGray;
      background-color: darken($blue, $amount: 5%);
    }
    &:active {
      color: $yellow;
      background-color: darken($blue, $amount: 5%);
    }
  }

  &__social {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
  }
  &__social-item img {
    margin: 0 15px;
    cursor: pointer;
    &:hover {
      transform:scale(1.5);
    }
  }
  &__copyright {
    min-height: 60px;
    width: 100%;
    background-color: $gray;
  }
  &__copyright-text {
    max-width: 1200px;
    margin: 0 auto;
    text-align: left; 
    padding-top: 24px;
    color: $lightGray;
    font-family: $Quest;
  }
    
}
