.programs {
  width: 100%;
  margin-bottom: 50px;
  &__container {
    max-width: 1720px;
    margin:   0 auto;

  }
  &__title {
    font-size: 36px;
    text-align: center;
    line-height: 1.25 ;
    margin-bottom: 60px;
  }

  &__wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
  }

  &__slider {
    max-width: 850px;
  }
  &__text-left {
    margin:0 10px 0 20px;
    padding-top: 40px;
  }
  &__text-right {
    margin:0 20px 0 10px;
    text-align: right;
    padding-top: 40px;
  }

  &__text-item {
    max-width: 330px;
    margin-bottom: 30px;
  }

  &__item-title {
    color: $blue;
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 20px;
  }

  &__item-subtitle {
    font-size: 18px;
    font-family: $Quest;
    line-height: 1.4;
  }

  &__slick-dots {
  position: absolute;
  content: '';
  top: 310px;
  left: 806px;
  }
  &__slick-dots li {
    width: 20px;
    height: 20px;
    font-size: 0;
    outline: none;
    border:none;
    border-radius: 2px;
    background-color: rgba(255, 255, 255, 0.2);
    margin-bottom: 10px;
  }
}

li.slick-active {
  background-color: rgba(0, 204, 204, 0.46);;
}


#slick-slide-control00 {
  display: none;
}

#slick-slide-control01 {
  display: none;
}

#slick-slide-control02 {
  display: none;
}

#slick-slide-control03 {
  display: none;
}

#slick-slide-control04 {
  display: none;
}



