.message {
  min-height: 400px;
  width: 100%;
  background: url(../img/message-bg.png) center ;
  background-size: 100%;
  margin-bottom: 60px;
  &__container {
    max-width: 1500px;
    min-height: 400px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 0 auto;
    
  }

  &__image {
    min-width: 292px;
    margin-right: 120px;
    margin-left: 20px;
  }
  
  &__image-wrapper {
    padding-top: 20px;
    display: flex;
    justify-content: center;
  }


  &__image-description {
    color: $white;


  }
  &__text-wrapper {
    max-width: 620px;
    margin-right: 330px;
  }
  &__title {
    font-family: $Quest;
    font-size: 30px;
    font-weight: 600;
    color: $white;
    line-height: 1.1;
    margin-bottom: 30px;
  }
  &__subtitle {
    font-size: 17px;
    color: $white;
    line-height: 1.8;
    font-weight:300;
  
  }
 
  &__button {
    width: 200px;
    height: 44px;
    background-color: $white;
    outline: none;
    font-weight: 400;
    font-size: 22px;
    border:transparent;
    border-radius: 20px;  
    color: $blue;
    flex-shrink: 0;
    margin-right: 20px;
    &:hover {
      color:$darkGray;
      background-color: darken($white, $amount: 10%);
    }
    &:active {
      color:$yellow;
      background-color: darken($white, $amount: 10%);
    }
  }
}

