.testimonials {
  min-height: 400px;
  width: 100%;
  background: url(../img/testimonials-bg.png) center ;
  background-size: 100%;

  &__container {
    max-width: 1260px;
    margin:   0 auto;
  }
  &__title {
    font-size: 36px;
    text-align: center;
    line-height: 1.25 ;
    margin-bottom: 30px;
  }

  &__wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: relative;
  }
  &__item {
    width: 35%;
    margin: 0 20px;
    text-align: center;
  }
  &__item-title {
    font-weight: 600;
    font-family: $Quest;
  }
  &__item-description {
    font-size: 16px;
    margin-bottom: 30px;
    line-height: 1.6;
    // word-spacing: -3px;
  }
  &__item-title {
    margin-bottom: 10px;
  }
  
}



